#header{
    width: 100%;
    height: 156px;
    background: #fff;
    box-shadow: rgba(0, 0, 0, 0.07) 0px 3px 4px 0px;
    .container{
        width: 100%;
        max-width: 1050px;
        height: 100%;
        margin: 0 auto;
        .row{
            width: 100%;
            &.row1{
                height: 36px;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                #aside{
                    ul{
                        display: flex;
                        li{
                            font-size: 12px;
                            &:first-child{
                                a{
                                    color: rgb(95,0,128);
                                    font-weight: 500;
                                }
                            }
                            a{
                                color: #333;
                                span{
                                    margin-left: 5px;
                                    img{
                                        width: 8px;
                                        height: 5px;
                                    }
                                }
                            }
                            i{
                                margin: 0 10px;
                            }
                        }
                    }
                }
            }
            &.row2{
                height: 64px;
                display: flex;
                align-items: center;    
                justify-content: space-between;
                > div{
                    height: 48px;
                    &.left{
                        width: 100%;
                        max-width: 325px;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        h1{
                            span{
                                margin-right: 10px;
                                img{
                                    width: 82px;
                                }
                            }
                            > a{
                                color: rgb(95,0,128);
                                font-size: 18px;
                                font-weight: 600;
                            }
                        }
                        i{
                            margin: 0 15px;
                            color: rgb(226, 226, 226);
                        }
                        > a{
                            color: rgba(181,181,181);
                            font-size: 18px;
                            font-weight: 600;
                        }
                    }
                    &.center{
                        width: 100%;
                        max-width: 400px;
                        min-width: 250px;
                        position: relative;
                        input{
                            z-index: 1;
                            position: relative;
                            width: 100%;
                            height: 100%;
                            border: 1px solid rgb(95,0,128);
                            font-size: 16px;
                            border-radius: 5px;
                            padding-left: 15px;
                            color: rgb(51, 51, 51);
                            &::placeholder{
                                color: #999;
                            }
                        }
                        button{
                            width: 48px;
                            z-index: 2;
                            position: absolute;
                            height: 100%;
                            top: 0;
                            right: 0;
                            background: transparent;
                            border: 0;
                            cursor: pointer;
                            img{
                                width: 35px;
                            }
                        }
                    }
                    &.right{
                        width: 100%;
                        max-width: 325px;
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        a{
                            margin-left: 18px;
                        }
                    }
                }
            }
            &.row3{
                height: 56px;
                display: flex;
                justify-content: space-between;
                > div{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    &.left{
                        height: 100%;
                        width: 100%;
                        max-width: 225px;
                        h1{
                            a{
                                img{
                                    width: auto;
                                }
                                span{
                                    font-size: 16px;
                                    color: rgb(90,90,90,);
                                    margin-left: 10px;
                                    font-weight: 500;
                                }
                                &:hover{
                                    span{
                                        color: rgb(95,0,128);
                                    }
                                }
                            }
                        }
                    }
                    &.center{
                        height: 100%;
                        width: 100%;
                        max-width: 600px;
                        #nav{
                            width: 100%;
                            ul{
                                display: flex;
                                li{
                                    width: 25%;
                                    text-align: center;
                                    a{
                                        font-size: 16px;
                                        color: rgb(90,90,90,);
                                        font-weight: 600;
                                        border-bottom: 1px solid transparent;
                                        padding-bottom: 1px;
                                        &:hover{
                                            color: rgb(95,0,128);
                                            border-color: rgb(95,0,128);
                                        }
                                    }
                                }
                            }
                        }
                    }
                    &.right{
                        width: 100%;
                        height: 100%;
                        max-width: 225px;
                        display: flex;
                        justify-content: flex-end;
                        span{
                            font-size: 13px;
                            padding: 6px 16px;
                            border: 1px solid #ccc;
                            border-radius: 12px;
                            strong{
                                color: rgb(95,0,128);
                            }
                            em{
                                color: #666;
                                margin-left: 5px;
                            }
                        }
                    }
                }
            }
        }
    }
}